.contact {
  max-width: 780px;
  margin: 0 auto;
  margin-bottom: 100px;
}

.contact__texts {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  margin: 0 auto;
  padding-bottom: 30px;
}

.contact__name {
  @include big;
  margin: 0;
}

.contact__address {
  @include middle;
  width: 210px;
  margin: 0;
  line-height: 1.5;
}

.contact__number {
  @include middle;
  width: 160px;
  margin: 0;
}

.contact__form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contact__list {
  @include list;
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
}

.contact__item {
  width: 49%;
  margin-right: 2%;

  &:nth-child(2n),
  &:last-child {
    margin-right: 0;
  }

  &--area {
    width: 100%;
  }
}

.contact__input {
  @include smallmiddle;
  width: 100%;
  margin-bottom: 30px;
  padding: 7px 10px 6px;
  color: $blue;
  border: 1px solid $gray;
  box-shadow: inset -2px 2px 2px $shadow;

  &::placeholder {
    color: rgba(#008dd2, 0.6);
  }
}

.contact__textarea {
  @include smallmiddle;
  width: 100%;
  margin-bottom: 30px;
  padding: 20px 10px 6px;
  color: $blue;
  border: 1px solid $gray;
  box-shadow: inset -2px 2px 2px $shadow;

  &::placeholder {
    color: rgba(#008dd2, 0.6);
  }
}

.contact__button {
  @include smallmiddle;
  font-weight: bold;
  width: 90px;
  padding: 10px 10px;
  color: $blue;
  border: 1px solid $blue;
  background-color: $white;
  cursor: pointer;

  &:hover,
  &:active {
    background-color: $blue;
    color: $white;
  }
}

@media (max-width: 600px) {
  .contact__button {
    width: 100%;
  }

  .contact__texts {
    flex-direction: column;
    align-items: center;
  }

  .contact__name {
    width: 100%;
    margin-bottom: 10px;
    text-align: center;
  }

  .contact__address {
    width: 100%;
    margin-bottom: 10px;
    text-align: center;
  }
}
