.other {
  max-width: 780px;
  margin: 0 auto;
  margin-bottom: 50px;

  &--product {
    margin-bottom: 200px;
  }
}

.other__title {
  @include middle;
  font-weight: normal;
  text-align: center;
  margin: 0 0 30px;
}

.other__list {
  @include list;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-end;
}

.other__item {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 220px;
  margin: 0 20px 20px;

  &:hover {
    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 2px;
      background-color: $blue;
    }
  }
}

.other__link {
  @include small;
  line-height: 1.1;
  color: $blue;
  text-align: center;
  text-decoration: none;
}

.other__image {
  margin-top: 10px;
}
