.post {
  max-width: 780px;
  margin: 0 auto;
  margin-bottom: 10px;
}

.post__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  padding-left: 35px;

  &::before {
    content: "";
    position: absolute;
    top: 5px;
    left: 0;
    width: 2px;
    background-color: $blue;
    height: calc(100% - 10px);
  }
}

.post__text {
  @include small;
  line-height: 2.2;
  margin: 0;
  text-align: justify;
}

.post__author {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.post__author-text {
  @include smallmiddle;
  width: 190px;
  margin: 0;
  margin-top: -20px;
  padding: 0 10px;
  text-align: center;
  font-weight: bold;
}

.post__author-list {
  @include list;
  display: flex;
  flex-wrap: wrap;
  width: 590px;
}

.post__author-item {
  display: flex;
  align-items: center;
  width: 295px;
  margin-bottom: 20px;
}

.post__author-img {
  margin-right: 20px;
}

.post__author-cover {
  display: flex;
  flex-direction: column;
  padding-right: 10px;
}

.post__author-name {
  @include smallmiddle;
  margin: 0;
  font-weight: bold;
  margin-bottom: 5px;
}

.post__author-position {
  @include small;
  margin: 0;
}
