.history {
  display: flex;
  flex-direction: column;
  width: 780px;
  margin: 0 auto;
  margin-bottom: 250px;
}

.history__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 60px 0;

  &:first-of-type {
    padding-top: 4px;
  }
}

.history__date-wrapper {
  display: flex;
  flex-direction: column;
  margin-top: -50px;
}

.history__date-next {
  display: flex;
  flex-direction: column;
  margin-bottom: -90px;
}

.history__text {
  @include smallmiddle;
  max-width: 650px;
  margin: 0;
  line-height: 1.6;
  text-align: justify;

  &--margin {
    margin-bottom: 30px;
  }
}

.history__date {
  position: relative;
  width: 92px;
  height: 92px;
  font-family: "Avanti-bold", "Arial", sans-serif;
  font-size: 2.0625em;
  line-height: 92px;
  text-align: center;
  border: 2px solid $blue;
  border-radius: 50%;
  background-color: $white;

  &::before,
  &::after {
    content: "";
    position: absolute;
    background-color: $blue;
    z-index: -1;
  }

  &--1993 {
    margin-top: -25px;

    &::before {
      width: 2px;
      height: 118px;
      top: 88px;
      left: 43px;
    }

    &::after {
      width: 690px;
      height: 2px;
      top: 206px;
      right: 43px;
    }
  }

  &--1994 {
    top: -142px;

    &::before {
      width: 2px;
      height: 724px;
      top: -176px;
      left: 43px;
    }

    &::after {
      width: 690px;
      height: 2px;
      top: 546px;
      left: 43px;
    }
  }

  &--1997 {
    &::before {
      width: 2px;
      height: 212px;
      top: -58px;
      left: 43px;
    }

    &::after {
      width: 690px;
      height: 2px;
      top: 152px;
      right: 43px;
    }
  }

  &--1998 {
    &::before {
      width: 2px;
      height: 212px;
      top: -58px;
      left: 43px;
    }

    &::after {
      width: 690px;
      height: 2px;
      top: 152px;
      left: 43px;
    }
  }

  &--2000 {
    top: -142px;

    &::before {
      width: 2px;
      height: 594px;
      top: -108px;
      left: 43px;
    }

    &::after {
      width: 690px;
      height: 2px;
      top: 484px;
      right: 43px;
    }
  }

  &--2001 {
    top: -132px;

    &::before {
      width: 2px;
      height: 474px;
      top: -60px;
      left: 43px;
    }

    &::after {
      width: 690px;
      height: 2px;
      top: 414px;
      left: 43px;
    }
  }

  &--2003 {
    &::before {
      width: 2px;
      height: 310px;
      top: -110px;
      left: 43px;
    }

    &::after {
      width: 690px;
      height: 2px;
      top: 200px;
      right: 43px;
    }
  }

  &--2004 {
    &::before {
      width: 2px;
      height: 270px;
      top: -90px;
      left: 43px;
    }

    &::after {
      width: 690px;
      height: 2px;
      top: 180px;
      left: 43px;
    }
  }

  &--2005 {
    margin-bottom: 30px;

    &::before {
      width: 2px;
      height: 130px;
      top: -128px;
      left: 43px;
    }

    &::after {
      width: 10px;
      height: 4px;
      bottom: -18px;
      left: 41px;
    }
  }

  &--2007 {
    &::before {
      width: 2px;
      height: 200px;
      top: 70px;
      left: 43px;
    }

    &::after {
      width: 690px;
      height: 2px;
      top: 268px;
      right: 43px;
    }
  }

  &--2008 {
    &::before {
      width: 2px;
      height: 238px;
      top: -75px;
      left: 43px;
    }

    &::after {
      width: 690px;
      height: 2px;
      top: 162px;
      left: 43px;
    }
  }

  &--2010 {
    &::before {
      width: 2px;
      height: 212px;
      top: -62px;
      left: 43px;
    }

    &::after {
      width: 690px;
      height: 2px;
      top: 150px;
      right: 43px;
    }
  }

  &--2011 {
    &::before {
      width: 2px;
      height: 210px;
      top: -60px;
      left: 43px;
    }

    &::after {
      width: 690px;
      height: 2px;
      top: 150px;
      left: 43px;
    }
  }

  &--2015 {
    margin-bottom: 30px;

    &::before {
      width: 2px;
      height: 60px;
      top: -60px;
      left: 43px;
    }

    &::after {
      width: 10px;
      height: 4px;
      bottom: -18px;
      left: 41px;
    }
  }
}

.history__image {
  &--left {
    align-self: flex-start;
  }

  &--right {
    align-self: flex-end;
  }
}

@media (max-width: 820px) {
  .history {
    width: 440px;
  }

  .history__texts {
    width: 340px;
  }

  .history__date {
    &--1993 {
      &::before {
        height: 220px;
      }

      &::after {
        width: 350px;
        top: 308px;
      }
    }

    &--1994 {
      &::before {
        height: 822px;
        top: -226px;
      }

      &::after {
        width: 350px;
        top: 596px;
      }
    }

    &--1997 {
      &::before {
        height: 264px;
        top: -86px;
      }

      &::after {
        width: 350px;
        top: 176px;
      }
    }

    &--1998 {
      &::before {
        height: 240px;
        top: -76px;
      }

      &::after {
        width: 350px;
        top: 164px;
      }
    }

    &--2000 {
      top: 0;

      &::before {
        height: 630px;
        top: -270px;
      }

      &::after {
        width: 350px;
        top: 358px;
      }
    }

    &--2001 {
      top: 0;

      &::before {
        height: 388px;
        top: -150px;
      }

      &::after {
        width: 350px;
        top: 236px;
      }
    }

    &--2003 {
      &::before {
        height: 508px;
        top: -210px;
      }

      &::after {
        width: 350px;
        top: 296px;
      }
    }

    &--2004 {
      &::before {
        height: 414px;
        top: -162px;
      }

      &::after {
        width: 350px;
        top: 250px;
      }
    }

    &--2005 {
      margin-bottom: 30px;

      &::before {
        height: 280px;
        top: -274px;
      }
    }

    &--2007 {
      &::before {
        height: 344px;
        top: 70px;
      }

      &::after {
        width: 350px;
        top: 413px;
      }
    }

    &--2008 {
      &::before {
        height: 336px;
        top: -124px;
      }

      &::after {
        width: 350px;
        top: 210px;
      }
    }

    &--2010 {
      &::before {
        height: 268px;
        top: -90px;
      }

      &::after {
        width: 350px;
        top: 176px;
      }
    }

    &--2011 {
      &::before {
        height: 216px;
        top: -64px;
      }

      &::after {
        width: 350px;
        top: 150px;
      }
    }

    &--2015 {
      margin-bottom: 30px;

      &::before {
        height: 120px;
        top: -110px;
      }
    }
  }
}
