.service {
  display: flex;
  flex-direction: column;
  max-width: 780px;
  margin: 0 auto;
}

.service__text-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  padding-top: 10px;
  padding-left: 25px;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 2px;
    height: 100%;
    background-color: $blue;
  }

  &--product {
    padding-top: 0;

    &::before {
      top: 10px;
      height: calc(100% - 20px);
    }
  }
}

.service__list-name {
  @include small;
}

.service__list {
  @include list;
  display: flex;
  flex-direction: column;
}

.service__item {
  padding-left: 5px;
}

.service__text {
  @include small;
  margin: 10px 0;
}

