.index-nav {
  margin-top: 120px;
}

.index-nav__list {
  @include list;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 auto;
  padding: 0 6px;
  max-width: 648px;
}

.index-nav__item {
  margin: 0 6px 12px;
}

.index-nav__link {
  @include verybig;
  position: relative;
  display: inline-block;
  padding: 25px 14px;
  width: 200px;
  min-height: 200px;
  color: $white;
  text-decoration: none;
  background-size: contain;

  &:hover,
  &:focus {
    color: $blue;
    background-color: $white;
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 200px;
    height: 200px;
    background-color: $blue;
    z-index: -1;
    opacity: 0.8;
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 200px;
    height: 200px;
    z-index: -2;
    background-size: contain;
  }

  &--company {
    &::after {
      background-image: url("");
    }
  }

  &--service {
    &::after {
      background-image: url("");
    }
  }

  &--product {
    &::after {
      background-image: url("");
    }
  }

  &--case {
    &::after {
      background-image: url("");
    }
  }

  &--blog {
    &::after {
      background-image: url("");
    }
  }

  &--contact {
    &::after {
      background-image: url("");
    }
  }
}
