*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  min-width: 480px;
  margin: 0;
  padding: 0;
  font-size: 1rem;
  line-height: 1;
  font-family: "Avanti", "Arial", sans-serif;
  color: $blue;
  font-style: normal;
  font-weight: normal;
  background-color: $white;
}

img {
  max-width: 100%;
  height: auto;
}
