.blog-menu {
  margin-bottom: 100px;
}

.blog-menu__list {
  @include list;
  display: flex;
  flex-direction: column;
  max-width: 780px;
  margin: 0 auto;
}

.blog-menu__item {
  position: relative;
  margin-top: -1px;
  border: 1px solid $bluelight;

  &:hover,
  &:focus {
    &::before {
      content: "";
      position: absolute;
      left: -1px;
      width: 2px;
      height: 100%;
      background-color: $blue;
    }
  }
}

.blog-menu__link {
  @include small;
  display: flex;
  flex-direction: column;
  padding: 0 75px;
  color: $blue;
  text-decoration: none;
}

.blog-menu__title {
  @include big;
  font-family: "Avanti-bold", "Arial", sans-serif;
}
