.product-gallery {
  margin-bottom: 50px;
}

.product-gallery__list {
  @include list;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 720px;
  margin: 0 auto;
}

.product-gallery__item {
  display: flex;
  margin: 0 5px 10px;
}

@media (max-width: 500px) {
  .product-gallery__item {
    width: 100%;
    margin: 0 0 10px;
  }

  .product-gallery__item img {
    width: 100%;
  }
}
