.mission {
  display: flex;
  max-width: 780px;
  margin: 0 auto;
  margin-bottom: 200px;
}

.mission__wrapper {
  display: flex;
  flex-direction: column;
  width: 330px;
  align-items: center;
}

.mission__director-photo {
  width: 245px;
  margin-top: 10px;
}

.mission__director-name {
  @include smallmiddle;
  width: 255px;
  margin: 0;
  line-height: 2;
  text-align: right;
}

.mission__direcor-caption {
  width: 200px;
}

.mission__text {
  @include smallmiddle;
  position: relative;
  margin: 0;
  padding-left: 45px;
  width: 440px;
  line-height: 2.3;
  text-align: justify;

  &::before {
    content: "";
    position: absolute;
    top: 10px;
    left: 0;
    width: 2px;
    height: calc(100% - 20px);
    background-color: $blue;
  }
}

@media (max-width: 820px) {
  .mission {
    align-items: center;
    flex-direction: column;
    margin-bottom: 100px;
  }
}
