.detail {
  max-width: 780px;
  margin: 0 auto;
  margin-bottom: 50px;
}

.detail__wrapper {
  position: relative;
  padding-left: 35px;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 2px;
    height: 100%;
    background-color: $blue;
  }
}

.detail__text {
  @include small;
  line-height: 3;
  margin: 0;
}

.detail__text img {
  padding-right: 30px;
}
