  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  max-width: 780px;
  margin: 0 auto; }

.vacancy__item {
  display: flex;
  flex-direction: column;
  margin-top: -1px;
  border: 1px solid #ddf0f9;
  background-color: #ffffff; }

.vacancy__title-wrapper {
  display: flex;
  z-index: 1; }

.vacancy__title {
  font-size: 1.125em;
  font-weight: bold;
  margin: 0;
  padding: 65px; }

.vacancy__row {
  position: relative;
  display: flex;
  max-width: 780px;
  top: 0;
  left: 0;
  margin-top: -148px;
  padding-left: 60px;
  padding-right: 10px;
  background-color: #ffffff;
  z-index: -1; }
  .vacancy__row::before {
    content: "";
    position: absolute;
    width: 2px;
    height: 100%;
    top: 0;
    left: 0;
    background-color: #008dd2; }

.vacancy__item:hover .vacancy__row {
  z-index: 1; }

.vacancy__item-wrapper {
  display: flex;
  flex-direction: column; }

.vacancy__item-title {
  font-size: 0.75em;
  font-weight: bold;
  margin: 0;
  padding: 10px 0 8px; }

.vacancy__item-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  padding-bottom: 10px; }

.vacancy__text {
  font-size: 0.75em;
  margin: 0;
  line-height: 1.5; }

.container {
  max-width: 1200px;
  padding: 0 20px;
  margin: 0 auto; }
