.rating {
  min-width: 480px;
  bottom: 0;
  border-top: 1px solid $bluelight;
  padding: 10px 0 30px;
  background-color: $white;
}

.rating__form {
  display: flex;
  align-items: center;
  max-width: 820px;
  padding: 0 20px;
  margin: 0 auto;
}

.rating__wrapper {
  display: flex;
  flex-direction: column;
}

.rating__title {
  @include smallmiddle;
  font-weight: bold;
  margin: 0;
  margin-bottom: 10px;
}

.rating__group {
  position: relative;
  width: 150px;
  height: 30px;
  margin: 0;
  margin-right: 30px;
  padding: 0;
  border: none;
  background-image: url("../img/off.svg");
  background-size: 30px 30px;
  background-repeat: repeat-x;
}

.rating__star {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.rating__label {
  position: absolute;
  height: 30px;
  top: 0;
  left: 0;
  background-size: 30px 30px;

  &:nth-of-type(1) {
    z-index: 5;
    width: 30px;
  }

  &:nth-of-type(2) {
    z-index: 4;
    width: 60px;
  }

  &:nth-of-type(3) {
    z-index: 3;
    width: 90px;
  }

  &:nth-of-type(4) {
    z-index: 2;
    width: 120px;
  }

  &:nth-of-type(5) {
    z-index: 1;
    width: 150px;
  }
}

.rating__star:checked + .rating__label,
.rating__star:hover + .rating__label {
  background-image: url("../img/on.svg");
}

.rating__list {
  @include list;
  display: flex;
  flex-wrap: wrap;
  width: 490px;
  margin: 0;
  margin-right: 15px;
  padding: 0;
  padding-top: 10px;
  border: none;
}

.rating__input {
  @include smallmiddle;
  width: 140px;
  margin-right: 15px;
  padding: 10px 10px 10px;
  color: $blue;
  border: 1px solid $gray;
  box-shadow: inset -2px 2px 2px $shadow;

  &::placeholder {
    color: rgba(#008dd2, 0.6);
  }
}

.rating__comment {
  @include smallmiddle;
  width: 180px;
  padding: 10px 10px 10px;
  color: $blue;
  border: 1px solid $gray;
  box-shadow: inset -2px 2px 2px $shadow;

  &::placeholder {
    color: rgba(#008dd2, 0.6);
  }
}

.rating__button {
  @include smallmiddle;
  font-weight: bold;
  width: 90px;
  margin-top: 10px;
  padding: 10px 10px;
  color: $blue;
  border: 1px solid $blue;
  background-color: $white;
  cursor: pointer;

  &:hover,
  &:active {
    background-color: $blue;
    color: $white;
  }
}

@media (max-width: 815px) {
  .rating__list {
    flex-direction: column;
    width: 35%;
  }

  .rating__input,
  .rating__comment {
    margin: 0;
    width: 100%;
  }
}
