.case {
  display: flex;
  flex-direction: column;
  max-width: 780px;
  margin: 0 auto;
}

.case__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  padding-top: 0;
  padding-left: 30px;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 2px;
    height: 100%;
    background-color: $blue;
  }
}
