.contact-nav {
  margin-bottom: 50px;
}

.contact-nav__wrapper {
  display: flex;
  align-items: flex-start;
}

.contact-nav__nav {
  width: 190px;
  min-width: 130px;
}

.contact-nav__links {
  display: flex;
  width: 780px;
  padding-top: 37px;
}

.contact-nav__link {
  @include smallmiddle;
  margin: 0 auto;
  padding: 10px;
  color: $blue;
  text-decoration: none;
  border: 1px solid $blue;

  &:hover,
  &:focus {
    background-color: $blue;
    color: $white;
  }
}
