.main-nav {
  @include container;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 25px 20px;

  &--index {
    justify-content: flex-end;
  }
}

.main-nav__logo {
  position: relative;
  margin-right: 45px;
  width: 160px;
  height: 64px;

  &::before {
    content: "";
    position: absolute;
    right: -30px;
    width: 2px;
    height: 64px;
    background-color: $blue;
  }

  &--index {
    margin-right: 58px;
  }
}

.main-nav__image {
  max-width: 160px;
}

.main-nav__list {
  @include list;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  max-width: 720px;
  width: 100%;
}

.main-nav__item {
  display: flex;
  justify-content: center;
  width: 120px;
  margin: 5px 0;
}

.main-nav__border {
  width: 80px;
  height: 2px;
  background-color: $blue;
  transition: 0.3s ease;
}

.main-nav__item--1active ~ .main-nav__border {
  margin-left: 20px;
}

.main-nav__item--2active ~ .main-nav__border {
  margin-left: 140px;
}

.main-nav__item--3active ~ .main-nav__border {
  margin-left: 260px;
}

.main-nav__item--4active ~ .main-nav__border {
  margin-left: 380px;
}

.main-nav__item--5active ~ .main-nav__border {
  margin-left: 500px;
}

.main-nav__item--6active ~ .main-nav__border {
  margin-left: 620px;
}

.main-nav__item--1:hover ~ .main-nav__border {
  margin-left: 20px;
}

.main-nav__item--2:hover ~ .main-nav__border {
  margin-left: 140px;
}

.main-nav__item--3:hover ~ .main-nav__border {
  margin-left: 260px;
}

.main-nav__item--4:hover ~ .main-nav__border {
  margin-left: 380px;
}

.main-nav__item--5:hover ~ .main-nav__border {
  margin-left: 500px;
}

.main-nav__item--6:hover ~ .main-nav__border {
  margin-left: 620px;
}

.main-nav__link {
  @include bigmiddle;
  display: inline-block;
  color: $blue;
  text-decoration: none;

  &--active {
    cursor: pointer;
  }
}

.main-nav__contacts {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-left: 45px;

  &::before {
    content: "";
    position: absolute;
    top: -11.5px;
    left: -30px;
    width: 2px;
    height: 64px;
    background-color: $blue;
  }

  &--index {
    margin-left: 0;
  }
}

.main-nav__text {
  @include middle;
  margin: 0;
  font-family: "Avanti", "Arial", sans-serif;
  font-weight: bold;
  line-height: 21px;
}

.main-nav__toggle {
  display: none;
}

@media (max-width: 1199px) {
  .header--nojs {
    margin-bottom: 372px;
  }

  .main-nav {
    position: relative;
    display: flex;
    justify-content: flex-end;
  }

  .main-nav__list {
    position: absolute;
    flex-direction: column;
    top: 115px;
    width: 480px;
    right: 0;
    order: 3;
    background-color: $white;
    z-index: 3;

    &--closed {
      display: none;
    }

    &--opened {
      display: flex;
    }
  }

  .main-nav__item {
    width: 100%;
    margin: 0;
    margin-top: -1px;
    border: 1px solid $bluelight;
  }

  .main-nav__border {
    display: none;
  }

  .main-nav__link {
    text-align: center;
    padding: 20px;
  }

  .main-nav__toggle {
    position: relative;
    display: block;
    width: 50px;
    height: 40px;
    padding: 0;
    background-color: $white;
    border: none;
    outline: none;

    &--closed {
      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 50px;
        height: 5px;
        background-color: $blue;
        box-shadow: 0 17px 0 $blue;
      }

      &::after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 50px;
        height: 5px;
        background-color: $blue;
      }
    }

    &--opened {
      &::before {
        content: "";
        position: absolute;
        top: 18px;
        left: 0;
        width: 50px;
        height: 5px;
        transform: rotate(45deg);
        background-color: $blue;
      }

      &::after {
        content: "";
        position: absolute;
        bottom: 18px;
        left: 0;
        width: 50px;
        transform: rotate(-45deg);
        height: 5px;
        background-color: $blue;
      }
    }
  }

  .main-nav__logo {
    margin-right: 58px;
  }

  .main-nav__contacts {
    margin-left: 0;
    margin-right: 10px;
  }

  .main-nav__list--nojs {
    display: flex;
    width: 100%;
  }

  .main-nav__toggle--nojs {
    display: none;
  }
}
