.menu {
  display: flex;
  justify-content: center;
  margin: 0 auto;
}

.menu__list {
  @include list;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  max-width: 792px;
}

.menu__item {
  margin: 0 6px 12px;
}

.menu__link {
  @include smallmiddle;
  position: relative;
  display: inline-block;
  padding: 25px 14px;
  color: $white;
  text-decoration: none;
  width: 186px;
  height: 186px;

  &:hover,
  &:focus {
    color: $blue;
    background-color: $white;
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 186px;
    height: 186px;
    background-color: $blue;
    z-index: -1;
    opacity: 0.8;
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 186px;
    height: 186px;
    z-index: -2;
    background-size: contain;
  }
}
