.blog-nav {
  display: flex;
  margin-bottom: 20px;
}

.blog-nav__nav {
  width: 190px;
  min-width: 110px;
}

.blog-nav__wrapper {
  padding-top: 15px;
  width: 780px;
}

.blog-nav__post-text {
  @include smallmiddle;
  margin: 0;
  padding-left: 35px;
  margin-bottom: 20px;
}

.blog-nav__post-title {
  @include bigmiddle;
  margin: 0;
  padding-left: 30px;
  line-height: 1.5;
}
