.request {
  position: fixed;
  bottom: 0;
  width: 100vw;
  padding-top: 10px;
  padding-bottom: 10px;
  border-top: 1px solid $bluelight;
  background-color: $white;
  z-index: 4;
}

.request__form {
  display: flex;
  align-items: center;
  justify-content: center;
}

.request__list {
  @include list;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-right: 15px;
}

.request__item {
  margin-right: 15px;
  margin-bottom: 5px;

  &:nth-child(3n) {
    margin-right: 0;
  }
}

.request__input {
  @include smallmiddle;
  width: 167px;
  padding: 7px 10px 6px;
  color: $blue;
  border: 1px solid $gray;
  box-shadow: inset -2px 2px 2px $shadow;

  &::placeholder {
    color: rgba(#008dd2, 0.6);
  }
}

.request__button {
  @include smallmiddle;
  font-weight: bold;
  width: 90px;
  padding: 6px 5px;
  color: $blue;
  border: 2px solid $blue;
  background-color: $white;
  cursor: pointer;

  &:hover,
  &:active {
    background-color: $blue;
    color: $white;
  }
}

@media (max-width: 675px) {
  .request {
    min-width: 480px;
  }

  .request__list {
    width: 80%;
    margin: 0;
  }

  .request__item {
    width: 30%;
    margin-right: 10px;
  }

  .request__input {
    width: 100%;
  }

  // .request__button {
  //   width: 100%;
  // }
}
