.product__wrapper {
  display: flex;
  align-items: flex-start;
  margin: 0 auto;
  margin-bottom: 40px;
  max-width: 780px;
}

.product__image {
  border-right: 2px solid $blue;
  padding-right: 50px;
  margin-right: 50px;
}

.product__links {
  @include list;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 20px;
}

.product__link-item {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-bottom: 25px;
  cursor: pointer;

  &:nth-child(3n) {
    margin-bottom: 0;
  }

  &:hover .product__link {
    color: $white;
    background-color: $blue;
  }
}

.product__link {
  @include middle;
  color: $blue;
  text-decoration: none;
  border: 1px solid $blue;
  padding: 10px 10px;
  text-align: center;

  &--details {
    width: 234px;
  }
}

.product__sub-menu {
  @include list;
  position: absolute;
  top: 40px;
  display: none;
}

.product__sub-item {
  display: flex;
  flex-direction: column;
}

.product__modal {
  @include middle;
  margin-top: -1px;
  width: 234px;
  color: $blue;
  text-decoration: none;
  border: 1px solid $blue;
  padding: 10px 10px;
  text-align: center;
  background-color: $white;

  &:hover {
    color: $white;
    background-color: $blue;
  }
}

.product__link-item:hover .product__sub-menu {
  display: block;
}

.product__text-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  max-width: 780px;
  margin: 0 auto;
  padding-left: 25px;

  &::before {
    content: "";
    position: absolute;
    top: 10px;
    left: 0;
    width: 2px;
    height: calc(100% - 20px);
    background-color: $blue;
  }
}

.product__text {
  @include small;
  margin: 0;
  text-align: justify;
  line-height: 3;
}

.product__list {
  @include list;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  max-width: 780px;
  margin: 0 auto;
  margin-bottom: 20px;
  padding-top: 25px;
}

.product__item-text {
  @include smallmiddle;
  line-height: 1.4;
  text-align: center;
  margin: 0;
}

.product__item-text b {
  position: relative;
  display: inline-block;
  font-size: 3em;
  font-weight: normal;
}

.product__item-bold {
  margin-top: -10px;
  bottom: -10px;
}

@media (max-width: 600px) {
  .product__list {
    flex-direction: column;
  }

  .product__item-text {
    border: 1px solid $bluelight;
    margin-top: -1px;
  }
}

@media (max-width: 800px) {
  .product__wrapper {
    flex-direction: column;
    margin-bottom: 80px;
  }

  .product__image {
    width: 100%;
    padding: 0;
    margin: 0;
    border: none;
  }

  .product__links {
    width: 100%;
  }

  .product__link-item {
    width: 100%;
  }

  .product__link {
    width: 100%;
  }

  .product__sub-menu {
    width: 100%;
  }

  .product__modal {
    width: 100%;
  }
}
