.page-nav {
  padding-top: 15px;
}

.page-nav__title {
  @include middle;
  margin: 0;
  margin-bottom: 15px;
}

.page-nav__return {
  @include smallmiddle;
  position: relative;
  margin-right: 10px;
  color: $blue;
  text-decoration: none;

  &--menu {
    &::before {
      content: "";
      position: absolute;
      width: 1px;
      top: 5px;
      left: -7px;
      height: 8px;
      background-color: $blue;
    }
  }
}

.page-nav__location {
  @include smallmiddle;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    width: 1px;
    top: 5px;
    left: -7px;
    height: 8px;
    background-color: $blue;
  }
}
