@mixin container {
  max-width: 1200px;
  padding: 0 20px;
  margin: 0 auto;
}

@mixin list {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin verysmall {
  font-size: 0.625em;
}

@mixin small {
  font-size: 0.75em;
}

@mixin smallmiddle {
  font-size: 0.9375em;
}

@mixin middle {
  font-size: 1.125em;
}

@mixin bigmiddle {
  font-size: 1.3125em;
}

@mixin big {
  font-size: 1.4375em;
}

@mixin verybig {
  font-size: 1.6875em;
}
