.reward {
  display: flex;
  max-width: 780px;
  margin: 0 auto;
}

.reward__list {
  @include list;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  margin-bottom: 300px;
}

.reward__item {
  margin-right: 40px;

  &:last-child {
    margin-right: 0;
  }
}

@media (max-width: 820px) {
  .reward__list {
    flex-direction: column;
    width: 100%;
    align-items: center;
  }

  .reward__item {
    margin: 0;
  }
}
