.vacancy {
  margin-bottom: 100px;
}

.vacancy__list {
  @include list;
  display: flex;
  flex-direction: column;
  max-width: 780px;
  margin: 0 auto;
}

.vacancy__item {
  display: flex;
  flex-direction: column;
  margin-top: -1px;
  border: 1px solid $bluelight;
  background-color: $white;
}

.vacancy__title-wrapper {
  display: flex;
  z-index: 1;
}

.vacancy__title {
  @include middle;
  font-weight: bold;
  margin: 0;
  padding: 65px;
}

.vacancy__row {
  position: relative;
  display: flex;
  max-width: 780px;
  top: 0;
  left: 0;
  margin-top: -148px;
  padding-left: 60px;
  padding-right: 10px;
  background-color: $white;
  z-index: -1;

  &::before {
    content: "";
    position: absolute;
    width: 2px;
    height: 100%;
    top: 0;
    left: 0;
    background-color: $blue;
  }
}

.vacancy__item:hover .vacancy__row {
  z-index: 1;
}

.vacancy__item-wrapper {
  display: flex;
  flex-direction: column;
}

.vacancy__item-title {
  @include small;
  font-weight: bold;
  margin: 0;
  padding: 10px 0 8px;
}

.vacancy__item-list {
  @include list;
  display: flex;
  flex-direction: column;
  padding-bottom: 10px;
}

.vacancy__text {
  @include small;
  margin: 0;
  line-height: 1.5;
}
