.company-nav {
  margin-bottom: 50px;
}

.company-nav__wrapper {
  display: flex;
}

.company-nav__nav {
  min-width: 190px;
}

.company-nav__list {
  @include list;
  display: flex;
  justify-content: space-around;
  width: 100%;
  max-width: 780px;
  padding-top: 30px;
}

.company-nav__item {
  display: flex;
  align-items: center;
}

.company-nav__link {
  @include middle;
  width: 122px;
  padding: 13px 10px;
  color: $blue;
  text-decoration: none;
  border: 1px solid $blue;
  text-align: center;

  &--active {
    color: $white;
    background-color: $blue;
    cursor: pointer;
  }

  &:active,
  &:hover {
    color: $white;
    background-color: $blue;
    cursor: pointer;
  }
}

@media (max-width: 650px) {
  .company-nav__nav {
    display: none;
  }
}
